  .inventory-main-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
  }


  .title-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .title-label{
    font-size: 16px;
    font-weight: bold;
    margin: 5px;
  }

  .crafting-button {
    height: 40px;
    width: 100px;
    margin: 10px;
  }
  
  .equipment-item {
    display: flex;
    align-items: center;
    margin: 7px;
    flex-direction: column;
  }
  
  .equipment-image {
    width: 32px;
    height: 32px;
    border: 1px solid grey;
    margin-bottom: 2px;
    padding: 5px;
  }
  
  .equipment-button {
    height: 20px;
  }
  
  .equipment-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .inventory-line {
    width: 100%;
    height: 1px;
    background-color: grey;
  }

  .inventory-container{
    overflow-y: auto;
    min-height: 100px;
  }
  
  .inventory-series {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .inventory-image {
    width: 32px;
    height: 32px;
    padding: 3px;
    margin: 10px;
  }

  .inventory-slot {
    width: 32px;
    height: 32px;
    border: 1px solid grey;
    padding: 3px;
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    border: 1px solid #000;
    z-index: 1000;
    width: 80%;
    max-width: 200px;
  }

  .textbox {
    background: white;
    padding: 20px;
    position: relative;
    width: 80%;
    min-width: 200px;
    max-width: 400px;
    z-index: 101;
  }

  
  .craftingbox {
    background: white;
    padding: 20px;
    position: relative;
    width: 95%;
    z-index: 101;
  }


  .popupoverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    background: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
    padding-left: 5px;
    z-index: 100;
  }


  .equipment-type {
    text-align: center;
    width: 75px;
  }

  .popupcrafting_costrow {
    display: flex;
    justify-content: space-between;
  }

  .popupcrafting_costtitle{
    font-size: 16px;
    font-weight: bold;
  }

  .singlecost_row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .singlecost_name{
    font-size: 16px;
    display: flex;
    align-items: center;
  }

  .singlecost_name img {
    height: 24px;
    margin-right: 5px;
  }

  .singlecost_value{
    margin-left: 20px;
    font-size: 16px;
    text-align: center;
  }

  .red-text{
    color: red;
  }